import { Link, useMatch, useNavigate } from "react-router-dom";
import styled from "styled-components";
import { motion, useAnimation, useScroll, useMotionValueEvent } from "framer-motion";
import { useState } from "react";
import { useForm } from "react-hook-form";

const Nav = styled(motion.nav)`
    position: fixed;
    z-index: 99;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 20px 60px;
    color: white;
    font-size: 14px;
`;

const Col = styled.div`
    display: flex;
    align-items: center;
`;

const Logo = styled(motion.svg)`
    width: 95px;
    height: 25px;
    fill: ${(props) => props.theme.red};
    margin-right: 50px;
    path {
        stroke-width: 6px;
        stroke: white;
    }
`;

const Items = styled.ul`
    display: flex;
    align-items: center;
`;

const Item = styled.li`
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 20px;
    color: ${(props) => props.theme.white.darker};
    font-weight: 600;
    transition: color 0.3s ease-in-out;
    &:hover {
        color: ${(props) => props.theme.white.lighter};
    }
`;

const Search = styled.form`
    position: relative;
    display: flex;
    align-items: center;
    color: white;
    svg {
        height: 25px;
    }
`;

const Circle = styled(motion.span)`
    position: absolute;
    left: 0;
    right: 0;
    bottom: -11px;
    width: 7px;
    height: 7px;
    margin: 0 auto;
    border-radius: 5px;
    background-color: ${(props) => props.theme.red};
`;

const Input = styled(motion.input)`
    position: absolute;
    z-index: -1;
    right: 0px;
    width: 210px;
    padding: 5px 10px 5px 40px;
    border: 1px solid ${(props) => props.theme.white.lighter};
    background-color: transparent;
    color: white;
    font-size: 12px;
    transform-origin: right center;
`;

const logoSvgVariants = {
    normal: {
        fillOpacity: 1,
    },
    active: {
        fillOpacity: [0, 1, 0],
        transition: {
            repeat: Infinity,
        },
    },
};

const logoPathVariants = {
    start: {
        pathLength: 0,
    },
    end: {
        pathLength: 1,
        transition: {
            duration: 5,
        },
    },
};

const navVariants = {
    top: { backgroundColor: "rgba(0, 0, 0, 0)" },
    scroll: { backgroundColor: "rgba(0, 0, 0, 1)" },
};

interface IForm {
    keyword: string;
}

function Header() {
    const homeMatch = useMatch("movies/*");
    const tvMatch = useMatch("tv/*");
    const [searchOpen, setSearchOpen] = useState(false);
    const inputAnimation = useAnimation();
    const navAnimation = useAnimation();
    const { scrollY } = useScroll();

    const { register, handleSubmit } = useForm<IForm>();
    const navigate = useNavigate();
    const onValid = (data: IForm) => {
        navigate(`/search?keyword=${data.keyword}`);
    };

    /**@function toggleSearch
     * 1. searchOpen의 값이 true이면 input창이 이미 열린 상태니까 scaleX:0으로 숨겨주고
     * 2. searchOpen의 값이 false이면 input창이 닫힌 상태니까 scaleX:1으로 열어줌
     * 3. setSearchOpen을 사용해서 searchOpen(bool타입)값을 반대 값으로 수정
     */
    const toggleSearch = () => {
        if (searchOpen) {
            inputAnimation.start({
                scaleX: 0,
            });
        } else {
            inputAnimation.start({
                scaleX: 1,
            });
        }

        setSearchOpen((prev) => !prev);
    };

    /**@event useMotionValueEvent
     * 1. framer-motion에서 지원
     * 2. scrollY 값이 변경되면 변경된 값으로 함수 처리
     * 3. 변경된 y 값이 80이 넘으면 navVariants의 "scroll"을 navAnimation에 적용
     * 4. 넘지않으면 navVariants의 "top"을 navAnimation에 적용
     */
    useMotionValueEvent(scrollY, "change", (y) => {
        if (y > 80) {
            navAnimation.start("scroll");
        } else {
            navAnimation.start("top");
        }
    });

    return (
        <Nav variants={navVariants} initial="top" animate={navAnimation}>
            <Col>
                <Logo
                    variants={logoSvgVariants}
                    initial="normal"
                    whileHover="active"
                    xmlns="http://www.w3.org/2000/svg"
                    width="1024"
                    height="276.742"
                    viewBox="0 0 1024 276.742"
                >
                    <motion.path
                        variants={logoPathVariants}
                        initial="start"
                        animate="end"
                        stroke="white"
                        strokeWidth="2"
                        d="M140.803 258.904c-15.404 2.705-31.079 3.516-47.294 5.676l-49.458-144.856v151.073c-15.404 1.621-29.457 3.783-44.051 5.945v-276.742h41.08l56.212 157.021v-157.021h43.511v258.904zm85.131-157.558c16.757 0 42.431-.811 57.835-.811v43.24c-19.189 0-41.619 0-57.835.811v64.322c25.405-1.621 50.809-3.785 76.482-4.596v41.617l-119.724 9.461v-255.39h119.724v43.241h-76.482v58.105zm237.284-58.104h-44.862v198.908c-14.594 0-29.188 0-43.239.539v-199.447h-44.862v-43.242h132.965l-.002 43.242zm70.266 55.132h59.187v43.24h-59.187v98.104h-42.433v-239.718h120.808v43.241h-78.375v55.133zm148.641 103.507c24.594.539 49.456 2.434 73.51 3.783v42.701c-38.646-2.434-77.293-4.863-116.75-5.676v-242.689h43.24v201.881zm109.994 49.457c13.783.812 28.377 1.623 42.43 3.242v-254.58h-42.43v251.338zm231.881-251.338l-54.863 131.615 54.863 145.127c-16.217-2.162-32.432-5.135-48.648-7.838l-31.078-79.994-31.617 73.51c-15.678-2.705-30.812-3.516-46.484-5.678l55.672-126.75-50.269-129.992h46.482l28.377 72.699 30.27-72.699h47.295z"
                    />
                </Logo>
                <Items>
                    <Item>
                        <Link to="/">Home {homeMatch && <Circle layoutId="circle" />}</Link>
                    </Item>
                    <Item>
                        <Link to="tv">TV Shows {tvMatch && <Circle layoutId="circle" />}</Link>
                    </Item>
                </Items>
            </Col>
            <Col>
                <Search onSubmit={handleSubmit(onValid)}>
                    <motion.svg
                        onClick={toggleSearch}
                        animate={{ x: searchOpen ? -177 : 0 }}
                        transition={{ type: "linear" }}
                        fill="currentColor"
                        viewBox="0 0 20 20"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            fillRule="evenodd"
                            d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
                            clipRule="evenodd"
                        ></path>
                    </motion.svg>
                    <Input
                        {...register("keyword", { required: true, minLength: 2 })}
                        animate={inputAnimation}
                        initial={{ scaleX: 0 }}
                        transition={{ type: "linear" }}
                        placeholder="Search for movie or tv show..."
                    ></Input>
                </Search>
            </Col>
        </Nav>
    );
}

export default Header;
